        name: [
          { required: true, message: "", trigger: "blur" },
          { required: true, validator: specialValidate, trigger: "blur" }
        ],
      },
      noticeRules: {
        title: [
          { required: true, message: "", trigger: "blur" },
          { required: true, validator: specialValidate, trigger: "blur" }
        ],
        content: [
          { required: true, message: "", trigger: "blur" },
          { required: true, validator: specialValidate, trigger: "blur" }
        ],
        startType: [
          { required: true, message: "", trigger: "blur" },
        ],
      },
      applyRules: [
        { required: true, message: "", trigger: "blur" },
        { required: true, validator: specialValidate, trigger: "blur" }
      ]
    };
  },
  methods: {
    //icon
    handleIconSuccess(res, file) {
      this.contentData.iconUrl = URL.createObjectURL(file.raw);
    }, 
    beforeIconUpload(file) {
      const isJPG = file.type === 'image/jpeg';
      const isPNG = file.type === 'image/png';
      const isLt50k = file.size / 1024 < 51;

      if (!isJPG&&!isPNG) {
        this.$message.error('icon JPG/PNG !');
      }
      if (!isLt50k) {
        this.$message.error('icon 50k!');
      }
      return (isJPG||isPNG) && isLt50k;
    },
    uploadIcon(data) {
      let param = new FormData();
      param.append("tipsId", 'tips_2');
      param.append("iconFile", data.file);
      this.$http
        .post("/scene/uploadTipsIcon", param)
        .then(response => {
          this.contentData.iconUrl = response.data.data && response.data.data;
          console.log(this.contentData.iconUrl)
          // this.dataTotal =
          //   response.data.data && response.data.data.totalElements;
          // this.dataList = response.data.data && response.data.data.content;
        })
        .catch(() => {
          //console.log(error);
        });
    },

    numberChange(value) {
      if (/^\d+$/.test(value)) {
        let number = Number(value);
        if (number >= 1 && number <= 10080) {
          this.isNoticeTimeError = false;
        } else {
          this.isNoticeTimeError = true;
        }
      } else {
        this.isNoticeTimeError = true;
      }
    },
    dateChange(isStart) {
      let startDate = Date.parse(this.contentData.time[0]);
      let endDate = Date.parse(this.contentData.time[1]);
      let now = Date.now();
      if (startDate < now) {
        this.isTimeError2 = true;
        this.isTimeError1 = false;
      } else if (endDate < now) {
        this.isTimeError2 = true;
        this.isTimeError1 = false;
      } else if (startDate >= endDate) {
        this.isTimeError1 = true;
        this.isTimeError2 = false;
      } else {
        this.isTimeError1 = false;
        this.isTimeError2 = false;
      }
    },
    noticeRadio2Choose() {
      this.noticeData.disappearTime = 120;
    },
    //
    handleDelete() {
      console.log('shanchu')
      let message = "" + this.baseData.name + "";
      this.$confirm(message, "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning",
        center: true
      })
        .then(() => {
          let param = {
            tipsId: this.baseData.tipsId,
            versionCode: this.baseData.versionCode
          };
          this.$http
            .post("/scene/delTips", param)
            .then(response => {
              if (response.data && response.data.code == "0") {
                this.$message({
                  type: "success",
                  message: "!"
                });
                this.$emit("tips-event", {
                  type: "home-page"
                });
              } else {
                if (response.data.msg) {
                  this.$message.error(response.data.msg);
                } else {
                  this.$message.error("");
                }
              }
            })
            .catch(() => {
              this.$message.error("");
            });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: ""
          });
        });
    },
    //
    xiajiaDialogClose(done) {
      if (typeof done === "function") {
        this.baseData.reason = "";
        done();
      } else if (done) {
        this.baseData.reason = "";
        this.xiajiaDialogVisible = false;
      } else {
        this.$refs["xiajiaData"].validate(valid => {
          if (valid) {
            let param = {
              tipsId: this.baseData.tipsId,
              versionCode: this.baseData.versionCode,
              pulloffReason: this.baseData.reason
            };
            this.$http
              .post("/scene/pulloffTips", param)
              .then(response => {
                if (response.data && response.data.code == "0") {
                  this.$message({
                    type: "success",
                    message: ""
                  });
                  this.xiajiaReason = this.baseData.reason;
                  this.baseData.reason = "";
                  this.baseData.status = 12;
                  this.xiajiaDialogVisible = false;
                } else {
                  if (response.data.msg) {
                    this.$message.error(response.data.msg);
                  } else {
                    this.$message.error("");
                  }
                }
              })
              .catch(() => {
                this.$message.error("");
              });
          } else {
            this.$message.warning("");
            return false;
          }
        });
      }
    },
    //
    xiajia() {
      this.xiajiaDialogVisible = true;
    },
    //
    shangjia() {
      let param = {
        tipsId: this.baseData.tipsId,
        versionCode: this.baseData.versionCode,
      };
      this.$http
        .post("/scene/putawayTips", param)
        .then(response => {
          if (response.data && response.data.code == "0") {
            this.baseData.status = 9;
            this.$message({
              type: "success",
              message: ""
            });
          } else {
            if (response.data.msg) {
              this.$message.error(response.data.msg);
            } else {
              this.$message.error("");
            }
          }
        })
        .catch(() => {
          this.$message.error("");
        });
    },
    //
    jinyongDialogClose(done) {
      if (typeof done === "function") {
        this.baseData.reason = "";
        done();
      } else if (done) {
        this.baseData.reason = "";
        this.jinyongDialogVisible = false;
      } else {
        this.$refs["jinyongData"].validate(valid => {
          if (valid) {
            let param = {
              tipsId: this.baseData.tipsId,
              versionCode: this.baseData.versionCode,
              disableReason: this.baseData.reason
            };
            this.$http
              .post("/scene/disableTips", param)
              .then(response => {
                if (response.data && response.data.code == "0") {
                  this.$message({
                    type: "success",
                    message: ""
                  });
                  this.jinyongReason = this.baseData.reason;
                  this.baseData.reason = "";
                  this.baseData.status = -1;
                  this.jinyongDialogVisible = false;
                } else {
                  if (response.data.msg) {
                    this.$message.error(response.data.msg);
                  } else {
                    this.$message.error("");
                  }
                }
              })
              .catch(() => {
                this.$message.error("");
              });
          } else {
            this.$message.warning("");
            return false;
          }
        });
      }
    },
    //
    jinyong() {
      this.jinyongDialogVisible = true;
    },
    //
    previewTest() {
      let url = location.origin + "/scene/getQrCode?url=" + this.testCardUrl;
      this.testImgSrc = url;
      this.testDialogVisible = true;
    },
    //
    goDataPage() {
      this.$emit("tips-event", {
        type: "data-page"
      });
    },
    //
    goEditPage() {
      let endTime = Date.parse(this.baseData.endTime);
      if (this.baseData.status !== 0 && Date.now() > endTime) {
        this.$confirm(", ", "", {
          confirmButtonText: "",
          showCancelButton: false,
          type: "warning",
          center: true
        });
      } else {
        this.stepNumber = 1;
        this.isEditPage = true;
        this.isCheckpage = false;
        this.isDetailpage = false;
      }
    },
    //
    deleteNotice() {
      this.noticeData = {
        title: "",
        content: "",
        startType: "",
        endType: "",
        endTime: "",
        timeSwitch: false
      };
      this.noticeDialogVisible = false;
    },
    //
    addNoticeButton() {
      this.noticeDialogVisible = true;
    },
    //
    deleteBtn() {
      this.noticeData = {
        title: "",
        content: "",
        startType: "",
        endType: "",
        endTime: "",
        timeSwitch: false
      };
      this.btnDialogVisible = false;
    },
    //
    addBtn() {
      this.btnDialogVisible = true;
    },
    //
    goHome() {
      this.$emit("tips-event", {
        type: "home-page"
      });
    },
    isEmpty(key) {
      if (typeof key === 'undefined' || key === '' || key === null) {
        return true;
      } else {
        return false;
      }
    },
    dataValidate (canEmpty) {
      let flag = false;
      this.$refs['nameData'].validate((valid) => {
        flag = valid
      });
      this.$refs['contentData'].validate((valid) => {
        flag = valid
      });
      this.$refs['textLinkData'].validate((valid) => {
        flag = valid
      });
      this.btnDialogVisible&&this.$refs['btnData'].validate((valid) => {
        flag = valid
      });
      this.noticeDialogVisible&&this.$refs['noticeData'].validate((valid) => {
        flag = valid
      });
      return flag;
    },
    //
    draftButton() {
      if (this.dataValidate(true)) {
        this.prepareEditData(0);
      }
    },
    //
    saveButton() {
      this.prepareEditData(1);
    },
    //
    async prepareEditData(status) {
      let currentState = 0;
      if (this.stepNumber === 2) {
        currentState = 3;
      }
      let param = {
        tipsId: this.baseData.tipsId,
        status: status,
        versionCode: this.baseData.versionCode,
        name: this.nameData.name,
        startTime: this.contentData.time[0],
        endTime: this.contentData.time[1],
        priority: this.contentData.priority,
        iconUrl: this.contentData.iconUrl,
        displayText: this.contentData.displayText,
        textLink: this.textLinkData,
        buttonName: this.btnDialogVisible?this.contentData.buttonName:'',
        buttonLink: this.btnDialogVisible?this.btnData:undefined,
        notice: this.noticeDialogVisible?this.noticeData:undefined
      };
      // if ((param.imeiData.fileName && param.imeiData.fileId) || !param.imeiData.fileName) {
        if(this.isAdd){
          this.addDataXhr(param);
        }else{
          this.editDataXhr(param);
        }

    },
    //excel
    async uploadExcel() {
      let formData = new FormData();
      formData.append("tipsId", this.baseData.id);
      formData.append("imeiFile", this.excelFile);
      return new Promise((resolve) => {
        this.$http
          .post('/scene/upoloadGreyImeiFile', formData)
          .then(response => {
            if (response.data && response.data.code == 0) {
              let fileId = response.data.data;
              resolve(fileId);
            } else {
              this.uploadLoading = false;
              if (response.data.msg) {
                this.$message.error(response.data.msg);
              } else {
                this.$message.error("");
              }
              resolve('');
            }
          })
          .catch(() => {
            this.uploadLoading = false;
            this.$message.error("");
            resolve('');
          });
      });
    },
    //xhr
    addDataXhr(param) {
      this.$http
        .post("/scene/addTips", param)
        .then(response => {
          this.uploadLoading = false;
          if (response.data && response.data.code == "0") {
            if (this.stepNumber === 1) {
              this.$message({
                type: "success",
                message: ""
              });
            } else {
              this.stepNumber++;
            }
          } else {
            if (response.data.msg) {
              this.$message.error(response.data.msg);
            } else {
              this.$message.error("");
            }
          }
        })
        .catch(() => {
          this.uploadLoading = false;
          this.$message.error("");
        });
    },
    // 
    editDataXhr(param) {
      this.$http
        .post("/scene/modifyTips", param)
        .then(response => {
          this.uploadLoading = false;
          if (response.data && response.data.code == "0") {
            if (this.stepNumber === 1) {
              this.$message({
                type: "success",
                message: ""
              });
            } else {
              this.stepNumber++;
            }
          } else {
            if (response.data.msg) {
              this.$message.error(response.data.msg);
            } else {
              this.$message.error("");
            }
          }
        })
        .catch(() => {
          this.uploadLoading = false;
          this.$message.error("");
        });
    },
    //
    lastStepButton() {
      this.stepNumber--;
    },
    //
    nextStepButton() {
      if (this.dataValidate(false)) {
        this.stepNumber++;
      }
    },
    //
    cancelEditButton() {
      this.$confirm("?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning",
        center: true
      })
        .then(() => {
          this.$emit("tips-event", {
            type: "home-page"
          });
        })
        .catch(() => { });
    },
    //xhr
    getDataXhr() {
      let param = {
        tipsId: this.baseData.tipsId,
        versionCode: this.baseData.versionCode
      };
      this.$http
        .post("/scene/getTipsDetail", param)
        .then(response => {
          if (
            response.data &&
            response.data.code == "0" &&
            response.data.data
          ) {
            let data = response.data.data
            this.nameData.name = data.name;
            this.contentData.time = [data.startTime,data.endTime];
            this.contentData.priority = data.priority;
            this.contentData.displayText = data.displayText;
            this.contentData.iconUrl = data.iconUrl;
            this.textLinkData = data.textLink;
            this.textLinkData.appMinVersion = this.textLinkData.appMinVersion&&parseInt(this.textLinkData.appMinVersion);
            this.contentData.buttonName = data.buttonName;
            this.noticeData = data.notice?data.notice:{};
            this.noticeDialogVisible = data.notice?true:false;
            this.btnData = data.buttonName?data.buttonLink:{};
            this.btnData.appMinVersion = this.btnData.appMinVersion&&parseInt(this.btnData.appMinVersion);
            this.btnDialogVisible = data.buttonName ? true : false;
            this.nowStatus = data.status;
            this.jinyongReason = data.disableReason;
            this.xiajiaReason = data.pulloffReason;
          }
        })
        .catch(() => {
          //console.log(error);
        });
    },
    //
    getDisappearTime(date, afterDate) {
      let d1 = new Date(date);
      let d2 = new Date(afterDate);
      let t = d2.getTime() - d1.getTime();
      if (t > 0) {
        return Math.round(t / 60000);
      }
    }
  },
  //
  mounted: function () {
    if (this.isCheckpage || this.isDetailpage) {
      this.isEditPage = false;
      this.stepNumber = 2;
    }
    if (typeof this.baseData.tipsId !== 'undefined') {
      this.getDataXhr();
    }
  },
  created: function () {
    // this.getDataXhr();
  }
};
</script>

