/* eslint-disable */
<template>
  <div class="header-class">
    <div class="left-input">
      <el-input placeholder="" v-model="tipsName" clearable></el-input>
    </div>
    <el-select v-model="statusKey" clearable placeholder="" class="left-select">
      <el-option v-for="item in statusList" :key="item.key" :label="item.name" :value="item.key">
      </el-option>
    </el-select>
    <el-button @click="searchData" icon="el-icon-search" class="left-button" type="primary"></el-button>
  </div>
</template>

<script>
export default {
  name: "header-component",
  props: {
    isCheckpage: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      tipsName: "",
      statusKey: "",
      statusList: [
        { key: 0, name: "" },
        { key: 1, name: "" },
        { key: 2, name: "" },
        { key: 3, name: "" },
        { key: 4, name: "" },
        { key: 5, name: "" },
        { key: 6, name: "" },
      ]
    };
  },
  methods: {
    searchData() {
      this.$emit("header-event", {
        type: "search-event",
        data: {
          name: this.tipsName,
          status: this.statusKey
        }
      });
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.header-class {
  width: 100%;
  height: 50px;
  margin-top: 30px;
}
.left-input {
  float: left;
  margin-left: 10px;
  width: 200px;
}
.left-button {
  float: left;
  margin-left: 20px;
}
.left-select {
  float: left;
  margin-left: 20px;
  width: 120px;
}
</style>